	module.exports = {"data":{"markdownRemark":{"html":"<h2>ForEach Loop</h2>\n<p>The ForEach loop executes a block of code for each item in a collection. The benefit of the foreach loop is you need not know how many items are within the collection to iterate through it; you simply tell your foreach loop to loop through the collection, as long as there are items within it.</p>\n<h3>Example</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-csharp\"><code>List<span class=\"token operator\">&lt;</span><span class=\"token keyword\">string</span><span class=\"token operator\">></span> Names <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">List</span><span class=\"token operator\">&lt;</span><span class=\"token keyword\">string</span><span class=\"token operator\">></span><span class=\"token punctuation\">{</span> <span class=\"token string\">\"Jim\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"Jane\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"Jack\"</span> <span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">foreach</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">string</span> name <span class=\"token keyword\">in</span> Names<span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">{</span>\n    Console<span class=\"token punctuation\">.</span><span class=\"token function\">WriteLine</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"We have \"</span> <span class=\"token operator\">+</span> name<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>    \n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Output:</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code>> We have Jim\n> We have Jane\n> We have Jack</code></pre>\n      </div>","fields":{"slug":"/csharp/foreach/"},"frontmatter":{"title":"ForEach Loop"}}},"pathContext":{"slug":"/csharp/foreach/"}}

